<template>
  <div>
    <div class="departmentelrow-div">
      <el-input
        placeholder="请输入科室或者医生名称"
        v-model="input"
        clearable
        class="onedeptinput"
      >
        <el-button
          icon="el-icon-search"
          style="background:#CBCBCB"
          slot="append"
        ></el-button>
      </el-input>
    </div>
    <div style="margin-top:5%"></div>
    <div
      v-for="item in threedeptinfo"
      v-bind:key="item.w3ksid"
      @click="deptlist(item)"
    >
      <el-row class="onedept" type="flex">
        <el-col :push="1" :span="22">{{ item.w3name }}</el-col>
        <el-col :span="2">
          <img src="../../assets/img/iconfont/arrow2x.png" />
        </el-col>
      </el-row>
      <el-divider class="oneddivider"></el-divider>
    </div>
  </div>
</template>
<script>
import Axios from "axios";

export default {
  name: "Category",
  data() {
    return {
      input: "",
      threedeptinfo: [],
      twodeptinfo: []
    };
  },
  methods: {
    getonedept() {
      Axios.get("/api/threedeptinfo")
        .then(res => {
          (this.threedeptinfo = res.data), console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    deptlist(item) {
      console.log(item.w3ksid);
      this.$router.push({
        name: "departlist",
        query: { w3ksid: item.w3ksid, w3name: item.w3name }
      });
    }
  },
  mounted: function() {
    this.getonedept();
  }
};
</script>
<style scoped>
@import "../../assets/css/gshawn.css";
</style>



